import uniLoadMore from '../../../components/uni-load-more.vue';

export default {
	 components: {//2注册组件
		uniLoadMore
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	data() {
		return {
			id:'',
			content:{},
			contentList:[],
			allPage:'',
			page:1,
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			contentIpt:'',
			token:'',
		}
	},
	onReachBottom:function(){
		let _this=this;
		_this.getComment();
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		let _this = this;
		_this.id = option.id;
	},
	mounted() {
		let _this = this;
		_this.eventDetail();
		_this.getComment();
	},
	methods: {
		// 列表
		eventDetail:function(){
			let _this=this;
			let data={
				id:_this.id
			};
			_this.$axios(_this.$baseUrl.eventDetail,data).then(res =>{
				if(res.data.status==1){
					_this.content = res.data.result
				}
			}).catch(error =>{
				
			})
		},
		//评论列表
		getComment:function(){
			let _this=this;
			let data={
				article_id:_this.id,
				page:_this.page,
			};
			_this.loadingType = 1;
			uni.showNavigationBarLoading();
			_this.$axios(_this.$baseUrl.getComment,data).then(res =>{
				if(res.data.status==1){
					if (res.data.result.comment == null ||res.data.result.comment==undefined ||res.data.result.comment=='' ) {//没有数据
					    _this.loadingType = 2;
					    uni.hideNavigationBarLoading();//关闭加载动画
					    return;
					}
					_this.page++
					_this.contentList = _this.contentList.concat(res.data.result.comment)
					_this.allPage = res.data.result.pages;
					_this.loadingType = 0;//将loadingType归0重置
					uni.hideNavigationBarLoading();//关闭加载动画
				}else{
					_this.loadingType = 2;
					uni.hideNavigationBarLoading();//关闭加载动画
				}
			}).catch(error =>{
				
			})
		},
		//发表评论
		conmmentAdds:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				article_id:_this.id,
				content:_this.contentIpt,
				anonymous:1,
			};
			console.log(JSON.stringify(data))
			_this.$axios(_this.$baseUrl.conmmentAdds,data).then(res =>{
				if(res.data.status==1){
					_this.contentIpt='';
					_this.contentList = [];
					_this.page = 1;
					let data1 = {
						article_id:_this.id,
						page:_this.page,
					};
					_this.$axios(_this.$baseUrl.getComment,data1).then(res =>{
						if(res.data.status==1){
							_this.contentList = res.data.result.comment
						}
					},(error) =>{
						
					})
					uni.showToast({
						title: res.data.msg,
						'icon':'success',
						'position':'center',
						duration: 2000
					})
				}else{
					uni.showToast({
						title: res.data.msg,
						'icon':'fail',
						'position':'center',
						duration: 2000
					})
				}
			}).catch(error =>{
				
			})
		}
	}
}
